import './Form.css'
import React from 'react'

const Form = () => {
  return (
    <div className='form'>
        <form>
            <label>Your Name</label>
            <input type='text' placeholder='Type your Name'></input>
            <label>Email</label>
            <input type='email' placeholder='Enter your email id'></input>
            <label>Subject</label>
            <input type='text' placeholder='Enter your Subject'></input>
            <label>Message</label>
            <textarea rows={6} placeholder='Type your message here'/>
            <button className='btn'>Submit</button>
        </form>
    </div>
  )
}

export default Form